@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;1,300;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Goldman:wght@700&display=swap);
body{
    margin:0;
    padding:0;
    height: 100%;
}
.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie{
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 10px;
    border-radius: 0 20%;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__poster{
    border-radius: 100%;
    position: relative;
    top: -50px;
    max-width: 120px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__data{
    color: rgb(110, 110, 110);
    margin-left: 10px;
    padding:10px;
    left: 180px;
    width: 300px;
    border-radius: 0 20%;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__title{
    color: black;
}
body{
    background-color: #2c3e50;
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
}

html,
body,
#root,
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100%;
    font-family: 'Goldman', cursive;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 50px;
}
.navigation {
    position: fixed;
    left: 20px;
    top: 20px;
    background-color: white;
    border-radius: 20px;
    width: 80px;
    height: 160px;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.navigation .navigation_button{
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    display: flex;
    color: #2c3e50;
    text-decoration: none;
    font-size: 400;
    
}
